$fontFamily: 'Montserrat', sans-serif;
$font: 18px;
$fontMedium: 24px;
$fontLong: 24px;
$fontSubTitle: 36px;
$fontTitle: 52px;
$fontTitleMD: 40px;
$color: #414f9e;
$colorLight: #fff;
$colorDark: #000;
$colorOrange: #ff6901;
$colorBlue: #9698c8;
$colorGreen: #337a3d;
$colorPurple: #bb05fa;
$colorLightBlue: #7060cd;
$colorAqua: #016b9a;

* {
	box-sizing: border-box;
}
html {
	overflow-x: hidden;
	scroll-behavior: smooth;
}
body {
	font-family: $fontFamily;
	font-size: $font;
}
//Global properties
a {
	color: inherit;
	&:hover {
		text-decoration: none;
	}
}
button {
	&:hover,
	&:focus {
		outline: none;
	}
}
input {
	&:hover,
	&:focus {
		outline: none;
	}
}

.btns {
	padding: 10px 40px;
	text-align: center;
	background: $color;
	color: $colorLight;
	font-weight: 400;
	// display: block;
	font-size: $fontLong;
	border: 1px solid $color;
	border-radius: 25px;
	-webkit-transition: all .25s linear;
	   -moz-transition: all .25s linear;
	    -ms-transition: all .25s linear;
	     -o-transition: all .25s linear;
	        transition: all .25s linear;
	&--light {
		color: $colorLight;
		border-color: $colorLight;
		padding: 5px 15px;
		font-size: $fontMedium;
		margin: 10px 0;
	}
	&--course {
		display: block;
		font-size: $fontMedium;
		// margin: 15px 0;
		margin-top: 40px;
		margin-bottom: 15px;
		width: 220px;
		padding: 10px;
	}
	&--secondary {
		padding: 5px 15px;
		background: transparent;
		font-size: $fontMedium;
		font-weight: 700;
		margin: 0 30px;
	}
	&--form {
		padding: 10px 60px;
	}
	&:hover {
		background: $colorLight;
		color: $color;
	}
}

.subtitle {
    /*    ,  
    **      */
    overflow: hidden;
    /*    , 
    **   left, right */
    text-align: center;
    /*        */
    font-size: 30px;
	padding-top: 40px;
	padding-bottom: 40px;
	font-style: normal;
	font-weight: 600;
	font-size: 36px;
	line-height: 44px;
	text-transform: uppercase;

color: #000000;
}
.subtitle span {
    /*      
    **    */
    display: inline-block;
    vertical-align: middle;
}
.subtitle:before,
.subtitle:after {
    /*     content, 
    **       */
    content: "";
    /*      -  
    **    -   */
    display: inline-block;
    vertical-align: middle;
    /*   100%    , 
    **      4  */
    width: 80%;
    height: 2px;
    /*     */
    background-color: #3D3B3B;
    /*     
    **  ,      */
    position: relative;
}
.subtitle:before {
    /*     100% ,   
    **      */
    margin-left: -100%;
    /*         */
    left: -18px;
}
.subtitle:after {
    /*     100% ,  
    **       */
    margin-right: -100%;
    /*         */
    right: -18px;
}

.global__btn{
	width: 251px;
	height: 70px;
	border: 2px solid #000000;
	box-sizing: border-box;
	font-style: normal;
	font-weight: 600;
	font-size: 18px;
	line-height: 22px;
	text-transform: uppercase;
	color: #000000;
	background: transparent;
	transition: all .25s linear;
	&:hover{
		background-color: #E5E5E5;
	}
}

