//menu
.logo {
	order: 1;
	padding: 0;
	
}
.navbar {
	background: $colorLight !important;
	padding: 0px !important;
	position: fixed;
	width: 100%;
	height: 90px !important;
	z-index: 999;
	overflow-wrap: normal;
	word-wrap: none;
	white-space: nowrap;
	font-family: $fontFamily;
	& .menu{
	&__tel{
		white-space: nowrap;
		font-style: normal;
		font-weight: 500;
		font-size: 24px;
		line-height: 29px;
		text-transform: uppercase;
		color: #000000;

	}
	&__logo {
		width: 180px;
		padding-bottom: 20px;
	}
	&__social {
		order: 4;
		& i{
			font-size: 29px;
			padding: 7px;
		}
		@media screen and (max-width: 768px) {
			order: 2;
		}
		& a {
			& i {
				color: $color;
				font-size: $fontLong;
				margin: 0 15px;
			}
		}
	}
}
}

.nav-row{
	display: flex;
	justify-content: space-between;
	width: 100%;
	align-items: center;
}
.nav-flex{
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    align-items: center !important;
    // flex-direction: column;
}
.navbar-nav{
	display: flex;
	justify-content: space-between;
	width: 100%;
	padding-left: 15px;
	padding-right: 15px;
}
.navbar-collapse {
	order: 2;
	width: 100%;
	justify-content: space-around;
}
.navbar-toggler {
	order: 4;
	@media screen and (max-width: 768px) {
		order: 4
	}
	&:focus {
		outline: none;
	}

}
.navbar-toggler-icon {
	font-size: $fontLong;
	line-height: 1.5;
	color: $color;
}
.nav-item{
	padding: 0 !important;
}
.nav-link {
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 22px;
	text-transform: uppercase;
	white-space: nowrap;
}

main{
	margin-top: 90px;
}

.main{
	height: 100vh;
	width: 100%;
	position: relative;
	&__wrap{
		height: auto;
		width: 100%;
		& img{
			width: 100%;
		}
	}
	&__aboutus{
		& .aboutus{
			background-color: #fff;
		}
	}
}

.slick-dots {
	padding-top: 40px;
	padding-bottom: 40px;
	position: static;
	height: 119px;
	li{
	background-color: transparent;
	height: 30px;
	width: 30px;
	border-radius: 50%;

	&:before {
		position: absolute;
		top: 4px;
		left: 4px;
		content: "" !important;
		width: 20px;
		height: 20px;
		text-align: center;
		background-color: #E5E5E5;;
		border-radius: 50%;
		opacity: 1 !important;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}
	}
	& .slick-active{
		border: 1px solid #000;
		&:before {
			position: absolute;
			top: 4px;
			left: 4px;
			content: "" !important;
			width: 20px;
			height: 20px;
			text-align: center;
			background-color: #000;
			border-radius: 50%;
			opacity: 1;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		}
	}

}

// Works

.works{
	margin-top: 119px;
	&__wraper{
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		&__item{
			position: relative;
			width: 360px;
			height: 360px;
			margin: 5px;
			overflow: hidden;
			display: flex;
			align-items: center;
			justify-content: center;
			transition: all .25s linear;
			& img{
				width: auto;
				min-width: 360px;
				height: auto;
				min-height: 360px;
				cursor: pointer;
			}
			&__plus{
				position: absolute;
				top: 46%;
				left: 46%;
				width: 52px;
				height: 52px;
				font-size: 40px;
				line-height: 52px;
				text-align: center;
				background: #FFFFFF;
				border: 1px solid #545454;
				border-radius: 50%;
				box-sizing: border-box;
				opacity: 0;
				transition: all .25s linear;
				cursor: pointer;
			}
			&:hover .works__wraper__item__plus{
				opacity: .6;
			}
			&:hover {
				transform: scale(1.05);
			}
		}
	}
}

.row__flex{
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 30px 0;
}

// Contact Us

.contact_us{
	position: relative;
	overflow: hidden;
	&__line{
		position: absolute;
		background-color: #E5E5E5;
		width: 100%;
		height: 100%;
		top: 106px;
		left: 0;
		z-index: -10;
	}
	&__flex{
		display: flex;
		align-items: center;
		justify-content: center;
		&__img{
			top: -106px;
			position: relative;
			display: flex;
			align-items: flex-end;
			justify-content: center;
			width: 50%;
			height: 490px;
		}
		&__form{
			height: 100%;
			display: flex;
			align-items: flex-start;
			justify-content: flex-end;
			flex-direction: column;
			&__title{
				font-style: normal;
				font-weight: 600;
				font-size: 36px;
				line-height: 44px;
				color: #000000;
				margin: 5px;
			}
			&__text{
				font-style: normal;
				font-weight: 500;
				font-size: 18px;
				line-height: 22px;
				text-align: center;
				margin: 5px;
				color: #000000;
			}
		}
	}
}

.form__contact_us{
	display: flex;
	align-items: flex-end;
	justify-content: flex-end;

	& input{
		height: 50px;
		width: 250px;
		background: #FFFFFF;
		border: 1px solid #000000;
		box-sizing: border-box;
		margin: 5px;
		padding: 14px 28px;
		font-style: normal;
		font-weight: 500;
		font-size: 16px;
		line-height: 22px;
		color: #ABA6A6;
		background-color: #fff;
	}
	.inputNames{
		width: 170px;
	}
	& button{
		width: 150px;
		height: 50px;
		font-size: 18px;
		margin: 5px;
		border: 1px solid #000000;
		background-color: #fff;
		font-weight: 500;	
	}
}

// catalog

.catalog{
	&__way{
		display: flex;
		// padding-bottom: 65px;
		&__link{
			font-style: normal;
			font-weight: bold;
			font-size: 18px;
			line-height: 22px;
			color: #C4C4C4;
			cursor: pointer;
			padding: 0 3px;
			&__active{
				color: #000;
			}
			&:hover{
				text-decoration: underline;
				color: #000;
			}
		}
	}
	&__colons{
		display: flex;
		align-items: center;
		justify-content: center;
		&__col{
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: auto;
			&__row{
				display: flex;
				align-items: center;
				justify-content: center;
				width: 100%;
				height: 247px;
				&__item{
					padding: 5px;
					overflow: hidden;
					width: 50%;
					min-width: 240px;
					height: 100%;
					z-index: 1;
					transition: all .25s linear;
					&:hover {
						transform: scale(1.05);
					}
					&__img{
						&__slide{
							position: absolute;
							background: rgba(61, 59, 59, 0.5);
							height: 49px;
							width: 100%;
							z-index: 100;
							top: 0;
							left: 0;
							transition: all .25s linear;
							display: flex;
							align-items: flex-end;
							justify-content: flex-start;
							cursor: pointer;
							&__bottom{
								position: absolute;
								background: rgba(61, 59, 59, 0.5);
								height: 49px;
								width: 100%;
								z-index: 100;
								bottom: 0;
								left: 0;
								transition: all .25s linear;
								display: flex;
								align-items: flex-start;
								justify-content: flex-start;
								cursor: pointer;
							}
							&__category{
								padding: 12px;
								font-family: 'Open Sans';
								font-style: normal;
								font-weight: 600;
								font-size: 18px;
								line-height: 25px;
								text-transform: uppercase;
								color: #FFFFFF;
							}
						}
						position: relative;
						width: 100%;
						height: 100%;
						& img{
							width: 100%;
							height: auto;
	
						}
						&:hover .catalog__colons__col__row__item__img__slide{
							height: 100%;
						}
						&:hover .catalog__colons__col__row__item__img__slide__bottom{
							height: 100%;
						}
					}
					
				}
			}
			&__row-big{
				display: flex;
				align-items: center;
				justify-content: center;
				width: 100%;
				height: 348px;
				&__item{
					padding: 5px;
					overflow: hidden;
					width: 100%;
					height: 100%;
					transition: all .25s linear;
					&:hover {
						transform: scale(1.03);
					}
					&__img{
						position: relative;
						width: 100%;
						height: 100%;
						& img{
							width: 100%;
							height: 100%;

						}
						&:hover .catalog__colons__col__row__item__img__slide{
							height: 100%;
						}
					}
				}
			}
		}
	}

}

// About Us

.aboutus{
	padding-top: 80px;
	padding-bottom: 38px;
	background: #E5E5E5;
	position: relative;
	&__flex{
		display: flex;
		align-items: center;
		justify-content: center;
		&__col{
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			padding: 10px;
			width: 50%;
			&__title{
				font-style: normal;
				font-weight: 600;
				font-size: 36px;
				line-height: 44px;
				padding-top: 30px;
				padding-bottom: 30px;
				color: #000000;
			}
			&__item{
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 10px;
				&__title{
					font-style: normal;
					font-weight: 500;
					font-size: 24px;
					line-height: 29px;
					color: #000000;
					padding-top: 10px;
					padding-bottom: 10px;
				}
				&__text{
					font-style: normal;
					font-weight: 400;
					font-size: 16px;
					line-height: 20px;
					color: #000000;
				}
				&__img{
					margin-right: 17px;
					width: 85px;
					height: auto;
				}
			}
			&__img{
				padding: 10px;
			}
		}
	}
	&__bottom-line{
		position: absolute;
		bottom: -106px;
		left: 0px;
		height: 106px;
		width: 100%;
		background: #E5E5E5;
		z-index: -10;
	}
}

// insta

.insta{
	background-color: transparent;
	padding-bottom: 48px;
	&__flex{
		display: flex;
		align-items: center;
		justify-content:flex-start;
		&__img{
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
			align-items: center;
			justify-content: flex-end;
			flex-wrap: wrap;
			&__item{
				padding: 3px;
				width: 190px;
				height: auto;
				transition: all .25s linear;
				&:hover {
					transform: scale(1.03);
				}
				& img{
					width: 100%;
					height: auto;
				}
			}
		}
		&__link{
			display: flex;
			align-items: center;
			justify-content: center;
			& a{
				&:hover{
					color: #000;
					opacity: 0.7;
				}
			}
			&__img{
				padding-left: 20px;
				padding-right: 13px;
			}
			&__text{
				font-style: normal;
				font-weight: bold;
				font-size: 24px;
				line-height: 29px;
			}
		}
	}
}



// Footer

