// Product

.product{
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	padding-top: 76px;
	&__flex{
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: flex-start;
	}
	&__way{
		display: flex;
		height: 100%;
		padding-bottom: 65px;
		&__link{
			font-style: normal;
			font-weight: 600;
			font-size: 14px;
			line-height: 17px;
			text-transform: uppercase;
			color: #C4C4C4;
			cursor: pointer;
			&__active{
				color: #000;
			}
			&:hover{
				text-decoration: underline;
				color: #000;
			}
		}
	}
	&__info{
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		width: 100%;
		height: 100%;
		&__foto{
			display: flex;
			align-items:flex-start;
			justify-content: center;
			width: auto;
			&__col{
				display: flex;
				align-items: flex-start;
				justify-content:  flex-start;
				flex-direction: column;
				height: auto;
				padding-right: 10px;
				&__img{
					&__small{
						height: 150px;
					width: 150px;
					
					position: relative;
					&__shadow{
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: rgba(61, 59, 59, 0.8);
						transition: all .25s linear;
						opacity: 0;
					}

					&:not(:first-child):not(:last-child) {
						margin-top: 10px;
						margin-bottom: 10px;
					}
					& img{
						width: 100%;
						height: auto;
					}
				}
				}
			}
			&__preview{
				width: 544px;
				height: 470px;
				overflow: hidden;
				
				& img{
					width: 100%;
					height: auto;
					display: table-cell;
					vertical-align: middle;
				}
			}
		}
		&__discription{
			display: flex;
			flex-direction: column;
			align-items:inherit;
			width: 100%;
			height: 100%;
			margin-left: 50px;
			&__title{
				font-style: normal;
				font-weight: 600;
				font-size: 24px;
