// catalog-consol

.catalog-consol{
	// padding-top: 109px;
	&__flex{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		&__items{
			display: flex;
			justify-content: flex-start;
			flex-wrap: wrap;
			&__item{
				margin: 7px;
				overflow: hidden;
				width: 32%;
				min-width: 240px;
				height: 306px;
				z-index: 1;
				transition: all .25s linear;
				position: relative;
				&:hover {
					transform: scale(1.05);
				}
				&__img{
					&__slide{
						position: absolute;
						background: rgba(61, 59, 59, 0.6);
						height: 49px;
						width: 100%;
						z-index: 100;
						top: 0;
						left: 0;
						transition: all .25s linear;
						display: flex;
						align-items: flex-end;
						justify-content: flex-start;
						cursor: pointer;
						
						&__bottom{
							position: absolute;
							background: rgba(61, 59, 59, 0.8);
							height: 49px;
							width: 100%;
							z-index: 100;
							bottom: 0;
							left: 0;
							transition: all .25s linear;
							display: flex;
							align-items: flex-start;
							justify-content: flex-start;
							cursor: pointer;
						}
						&__category{
							padding: 12px;
							font-family: 'Open Sans';
							font-style: normal;
							font-weight: 600;
							font-size: 18px;
							line-height: 25px;
							text-transform: uppercase;
							color: #FFFFFF;
						}
					}
					position: relative;
					width: 100%;
					height: 100%;
					& img{
						width: 100%;
						height: auto;

					}
					&:hover .catalog-consol__flex__items__item__img__slide{
						height: 100%;
					}
					&:hover .catalog-consol__flex__items__item__img__slide__bottom{
						height: 100%;
					}
				}
				
			}
		}
	}
}

