@media screen and (max-width: 768px) {
	
}
@media screen and (max-width: 576px) {
	.global__btn{
		width: 328px;
		height: 68.2px;
	}

	.row__flex{
		padding: 10px 0 !important;
	}

	.subtitle {
		font-size: 30px;
		padding-top: 23px;
		padding-bottom: 23px;
		font-size: 24px;
		line-height: 29px;
	}

	//menu
.logo {
	// display: none;
	order: 1;
	width: 106px;
	height: auto;
	& img{
		width: 100%;
		height: auto;
	}
}
.nav-flex{
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    align-items: center !important;
    // flex-direction: column;
    order: 3;
	width: 100%;
}
.navbar-toggler-icon{
    color: #000000 !important;
    opacity: 2;
}

.navbar {
	background: $colorLight !important;
	padding: 0px !important;
	position: fixed;
	width: 100%;
	height: 90px !important;
	z-index: 999;
	overflow-wrap: normal;
	word-wrap: none;
	white-space: nowrap;
	font-family: $fontFamily;
    justify-content: center;
    flex-direction: column;
    background: #fff !important;
	& .menu{
	&__tel{
		white-space: nowrap;
		font-style: normal;
		font-weight: 500;
		font-size: 18px;
		line-height: 20px;
		text-transform: uppercase;
		color: #000000;

	}
	&__logo {
		width: 180px;
		padding-bottom: 20px;
	}
	&__social {
		order: 1;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		& i{
			font-size: 24px;
			padding: 7px;
		}
		// @media screen and (max-width: 768px) {
		// 	order: 1;
		// }
		& a {
			& i {
				color: $color;
				font-size: $fontLong;
				margin: 0 15px;
			}
		}
	}
}
}
.navbar-nav{
	display: flex;
	justify-content: space-between;
	width: 100%;
	padding-left: 15px;
	padding-right: 15px;
}
.navbar-collapse {
	order: 2;
	width: 100%;
	height: auto;
	justify-content: space-around;
}
.navbar-toggler {
	order: 4;
	@media screen and (max-width: 768px) {
		order: 4
	}
	&:focus {
		outline: none;
	}

}
.navbar-toggler-icon {
	font-size: $fontLong;
	line-height: 1.5;
	color: $color;
}
.nav-item{
	padding: 0 !important;
}
.nav-link {
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 22px;
	text-transform: uppercase;
	white-space: nowrap;
}

// main

main{
	margin-top: 90px;
}

.main{
	height: auto;
}

.slick-dots {
	padding-top: 10px;
	padding-bottom: 10px;
	height: 30px;
	li{
	height: 12px;
	width: 12px;

	&:before {
		top: 3px;
		left: 3px;
		width: 6px;
		height: 6px;
	}
	}
	& .slick-active{
		&:before {
			top: 2px;
			left: 2px;
			width: 6px;
			height: 6px;
		}
	}

}

// Works

.works{
	margin-top:0px;
	&__wraper{
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		&__item{
			overflow: hidden;
			width: 100%;
			height: auto;
			margin: 0px;
			padding: 5px;
			& img{
				width: 100%;
				min-width: 0px;
				height: auto;
				min-height: 220px;
				cursor: pointer;
			}
			&__plus{
				display: none;
			}
			&:hover .works__wraper__item__plus{
				opacity: .6;
			}
			&:hover {
				transform: scale(1.0);
			}
		}
	}
}

.row__flex{
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 30px 0;
}

// Contact Us

.contact_us{
	position: relative;
	overflow: hidden;
	&__line{
		position: absolute;
		background-color: #E5E5E5;
		width: 100%;
		height: 100%;
		left: 0;
		z-index: -10;
	}
	&__flex{
		flex-direction: column;
		&__img{
			top: 0px;
			position: relative;
			display: flex;
			align-items: flex-end;
			justify-content: center;
			width: 70%;
			height: 200px;
			& img{
				width: 100%;
				height: auto;
			}
		}
		&__form{
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			&__title{
				font-weight: 600;
				font-size: 24px;
				line-height: 29px;
			}
			&__text{
				font-size: 14px;
				line-height: 17px;
			}
		}
	}
}

.form__contact_us{
	align-items: center;
	justify-content: center;
	flex-direction: column;
	& input{
		width: 327.42px;
		height: 50px;
	}
	.inputNames{
		width: 327.42px;
		height: 50px;
	}
	& button{
		width: 327.42px;	
	}
}

.catalog{
	&__way{
		display: flex;
		// padding-bottom: 65px;
		&__link{
			font-style: normal;
			font-weight: bold;
			font-size: 18px;
			line-height: 22px;
			color: #C4C4C4;
			cursor: pointer;
			padding: 0 3px;
			&__active{
				color: #000;
			}
			&:hover{
				text-decoration: underline;
				color: #000;
			}
		}
	}
	&__colons{
		flex-direction: column;
		&__col{
			flex-direction: column;
			width: 100%;
			height: auto;
			&__row{
				width: 100%;
				height: 160px;
				&__item{
					min-width: 160px;
					&:hover {
						transform: scale(1.0);
					}
				}
			}
			&__row-big{
				width: 100%;
				height: auto;
				max-height: 300px;
				&__item{
					&:hover {
						transform: scale(1.0);
					}
				}
			}
		}
	}

}

// About Us

.aboutus{
	padding-top: 0px;
	padding-bottom: 38px;
	&__flex{
		flex-direction: column;
		&__col{
			flex-direction: column;
			padding: 10px;
			width: 100%;
			&__title{
				font-size: 24px;
				line-height: 29px;
				padding-top: 20px;
				padding-bottom: 20px;
			}
			&__item{
				flex-direction: column;
				padding: 10px;
				text-align: center;
				&__title{
					font-size: 24px;
					line-height: 29px;
					padding-top: 10px;
					padding-bottom: 10px;
				}
				&__text{
					font-style: normal;
					font-weight: 400;
					font-size: 18px;
					line-height: 20px;
					color: #000000;
				}
				&__img{
					margin-right: 17px;
					width: 85px;
					height: auto;
				}
			}
			&__img{
				padding: 10px;
				width: 100%;
				height: auto;
				overflow: hidden;
				& img{
					width: 100%;
					height: auto;
				}
			}
		}
	}
	&__bottom-line{
		position: absolute;
		bottom: -106px;
		left: 0px;
		height: 106px;
		width: 100%;
		background: #E5E5E5;
		z-index: -10;
	}
}

.insta{
	display: none;
}

// Footer

.footer{
	font-size: 14px;
	line-height: 17px;
	z-index: 0;
	&__line{
		height: 97px;
		width: 100%;
	}
	&__row{
		justify-content:space-between;
		flex-direction: column;
		width: 100%;
		padding: 55px 0;
		&__links{
			width: 100%;	
			&__col{
				z-index: 20;
				width: 100%;	
				&__menu{
					padding-top: 6px;
					padding-bottom: 6px;
				}
				&__info{
					width: 100%;	
					&__social{
						padding-top: 20px;
						width: 100%;
					}
					&__title{
						padding-top: 6px;
						padding-bottom: 6px;
					}
					&__item{
						padding-top: 6px;
						padding-bottom: 6px;
						font-style: normal;
						font-weight: 600;
						font-size: 24px;
						line-height: 29px;
					}
					&__social{
						display: flex;
						align-items:center;
						justify-content: center;
						& i{
							// width: 100%;
							margin-left: 8px;
							margin-right: 8px;
							font-size: 30px;
						}
					}
				}
			}
		}
	}
}


// Product

.product{
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	padding-top: 36px;
	&__flex{
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: flex-start;
	}
	&__way{
		display: flex;
		height: 100%;
		padding-bottom: 65px;
		&__link{
			font-style: normal;
			font-weight: 600;
			font-size: 14px;
			line-height: 17px;
			text-transform: uppercase;
			color: #C4C4C4;
			cursor: pointer;
			&__active{
				color: #000;
			}
			&:hover{
				text-decoration: underline;
				color: #000;
			}
		}
	}
	&__info{
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-direction: column;
		width: 100%;
		height: 100%;
		&__foto{
			display: flex;
			align-items:flex-start;
			justify-content: center;
			flex-direction: column;
			width: auto;
			&__col{
				flex-direction: row;
				height: auto;
				padding-right: 0px;
				order: 2;
				padding-top: 5px;
				&__img{
					&__small{
					height: 76px;
					width: 76px;
					position: relative;
					&__shadow{
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: rgba(61, 59, 59, 0.8);
						transition: all .25s linear;
						opacity: 0;
					}

					&:not(:first-child):not(:last-child) {
						margin-top: 0px;
						margin-bottom: 0px;
						margin-left: 5px;
						margin-right: 5px;
					}
					& img{
						width: 100%;
						height: auto;
					}
				}
				}
			}
			&__preview{
				width: 328px;
				height: 325px;
				overflow: hidden;
				order: 1;
				& img{
					width: 100%;
					height: auto;
					display: table-cell;
					vertical-align: middle;
				}
			}
		}
		&__discription{
			display: flex;
			flex-direction: column;
			align-items:inherit;
			padding-top: 30px;
			width: 100%;
			height: 100%;
			margin-left: 0px;
			&__title{
				font-style: normal;
				font-weight: 600;
				font-size: 24px;
				line-height: 29px;
				text-transform: uppercase;
				color: #000000;
				padding-bottom: 6px;
			}
			&__wrapper{
				display: flex;
				flex-direction: column;
				
			}
			&__item{
				display: flex;
				&__title{
					color: #545454;
					padding-top: 22px;
					font-style: normal;
					font-weight: 500;
					font-size: 14px;
					line-height: 17px;
					text-transform: uppercase;
					min-width: 100px;
					}
				&__value{
					color: #000;
					padding-top: 22px;
					font-style: normal;
					font-weight: 500;
					font-size: 14px;
					line-height: 17px;
					text-transform: uppercase;
				}
			}
			&__price{
				font-style: normal;
				font-weight: 500;
				line-height: 22px;
				margin-top: 100px;
				height: 100%;
				display: flex;
				&__title{
					color: #545454;
					font-size: 18px;
				}
				&__number{
					font-size: 20px;
					font-weight: 600;
					padding-right: 5px;
					padding-left: 5px;
					text-transform: uppercase;
					color: #000000;
				}
			}
		}
	}
	&__big-description{
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-direction: column;
		width: 100%;
		padding-top: 40px;
		max-width: 704px;
		&__title{
			font-style: normal;
			font-weight: 600;
			font-size: 24px;
			line-height: 29px;
			text-transform: uppercase;
			color: #000000;
			margin-bottom: 10px;
		}
		&__text{
			font-style: normal;
			font-weight: normal;
			font-size: 18px;
			line-height: 22px;
			color: #5F5F5F;
		}
	}
	&__line{
		width: 100%;
		height: 126px;
		background-color: #fff;
	}
}

.shadow{
	opacity: 1;
}


}